.selected {
    font-weight: bold;
    background: #75A0DE;
    color:white;
}

.paginator {
    margin-bottom: 50px;
    text-align: center;
}

.page{
    padding: 4px 8px 4px 8px;
    cursor: pointer;
    border: 1px solid #6559BC;
    margin-left: 2px;
    margin-right: 2px;
}

.button {
    padding: 6px;
    cursor: pointer;
    background-color: black;
    color: white;
}

.points {
    font-size: 30px;
  
    color: #6559BC;
}