.user {
    outline: 0px solid red;
    display: flex;
    margin-left: 100px;
    margin-bottom: 10px;
}

.img_button {
    outline: 0px solid;
}

.button {
    background: #00B666;
    color: white;
    width: 85px;
    height: 26px;
}

.button:hover {
    border: 1px solid green;
}

.button.color {
    background: red;
}

.user img{
    height: 70px;
    width: 70px;
    border-radius: 50px;
    margin-bottom: 5px;
    margin-left: 5px;
}

.aboutUser {
    outline: 0px solid green;
    display: flex;
    width: 600px;
    height: 70px;
    justify-content: space-between;
    padding: 10px;
    border: 1px solid #ADACAF;
    border-radius: 10px;
    background: #E6E8F6;
}

.name_status {

}

.fullName {
    margin-bottom: 20px;
    font-size: 20px;
    font-weight: 500;
}

.status {
    font-size: 15px;
}

.country {
    margin-bottom: 15px;
}




.userIcons {
    font-size: 80px;
    margin-right: 10px;
    color: #75A0DE;
}